export default class TabContent extends Component {
  components = {
      'home': HomeContent
  }

  render(){
    const { activeItem } = this.props
    const TagName = this.components[activeItem]

    if(TagName){
      return (
        <TagName />
      )
    }
    else{
      return (
        <Container>
          <p>Not Found, But Not 404. Maybe under construction.</p>
        </Container>
      )
    }
  }
}
